.gg-section {
	position: relative;
	min-height: 1520px;
	margin-top: 100px;
	margin-bottom: 50px;
	@include sm {
		min-height: 850px;
		margin-top: 0; }
	&__alphabet {
		position: absolute;
		top: 0;
		left: 50%;
		height: 1520px;
		transform: translate(-50%, 0);
		z-index: -1;
		img {
			height: 100%; } }
	&__inner {
		padding-top: 215px;
		z-index: 1;
		&.inner {
			@include md {
				padding: 215px 119px 0; }
			@include sm {
				padding: 215px 15px 0; } } }

	&__title {
		margin: 0 auto 65px 0;
		max-width: 410px;
		@include md {
			max-width: 100%;
			margin: 0 auto 60px; }
		.title-md {
			margin-bottom: 66px; }
		span {
			font-family: $SFUIDisplay;
			font-size: 18px;
			font-weight: 300;
			line-height: 30px;
			letter-spacing: 0.9px; } }
	&__left-col {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include md {
			flex-wrap: wrap;
			justify-content: center; } }
	&__up-block {
		@include md {
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%;
			margin-bottom: 30px; }
		@include sm {
			justify-content: center; } }
	&__image-face {
		position: relative;
		width: 150px;
		height: 150px;
		border-radius: 50%;
		background-color: #fff;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		@include sm {
			flex: none;
			width: 85px;
			height: 85px; }
		&::before {
			position: absolute;
			content: '';
			background-color: $yellow;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			bottom: -60px;
			left: 20%;
			transform: translate(-50%, -50%);
			z-index: 2;
			@include sm {
				width: 30px;
				height: 30px; } }
		&::after {
			position: absolute;
			content: '';
			background-color: $yellow;
			width: 15px;
			height: 15px;
			border-radius: 50%;
			bottom: -90px;
			left: 35%;
			transform: translate(-50%, -50%);
			z-index: 2; } }
	&__title-block {
		margin: 67px 0 0 98px;
		max-width: 359px;
		color: $grey;
		font-family: $SFUIDisplay;
		font-weight: 300;
		line-height: 30px;
		letter-spacing: 0.9px;
		@include md {
			margin: 0 0 0 40px; }
		.title-sm {
			font-size: 18px;
			margin-bottom: 30px; }
		p {
			font-size: 14px;
			letter-spacing: 0.7px; } }
	&__image-house {
		width: 420px;
		height: 420px;
		border-radius: 50%;
		background-color: #fff;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover; }
	&__right-col {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 20px;
		@include md {
			flex-wrap: wrap;
			justify-content: center;
			flex-direction: column-reverse; }
		@include sm {
			display: none; } }
	&__bottom-block {
		@include md {
			display: flex;
			align-items: flex-start;
			justify-content: center;
			flex-direction: row-reverse;
			margin-top: 20px;
			margin-bottom: 30px; }
		.gg-section__title-block {
			margin-top: 0; }
		.gg-section__image-face {
			position: relative;
			margin: 0 0 0 auto;
			&::before {
				bottom: -70px;
				left: 77%; }
			&::after {
				bottom: -95px;
				left: 60%; } }
		.gg-section__title-block_margin {
			margin-left: 0;
			margin-right: 98px;
			@include md {
				margin-left: auto;
				margin-right: {} } } } }
