.ff-section {
	position: relative;
	min-height: 1900px;
	margin-top: 200px;
	@include md {
		margin-top: 0; }
	@include sm {
		margin-top: 150px; }
	&__alphabet {
		position: absolute;
		top: 0;
		left: 50%;
		height: 2120px;
		transform: translate(-50%, 0);
		z-index: -1;
		@include sm {
			height: 2250px; }
		img {
			height: 100%; } }
	&__inner {
		padding-top: 85px;
		z-index: 1;
		&.inner {
			@include sm {
				padding: 0; } } }

	&__title {
		margin: 0 0 150px auto;
		max-width: 410px;
		@include md {
			max-width: 100%;
			margin: 0 auto 130px;
			padding: 70px 15px 0; }
		@include xs {
			padding-top: 170px;
			margin-bottom: 40px; }
		.title-md {
			margin-bottom: 66px; }
		span {
			font-family: $SFUIDisplay;
			font-size: 18px;
			font-weight: 300;
			line-height: 30px;
			letter-spacing: 0.9px; } }
	&__wrap {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		@include sm {
			flex-wrap: wrap;
			flex-direction: column;
			align-items: center; } }
	&__col {
		flex: 1 1 48%;
		margin-right: 20px;
		text-align: center;
		font-family: $SFUIDisplay;
		@include sm {
			margin-right: 0;
			width: 100%; } }
	&__accord-btn {
		position: relative;
		display: block;
		background: none;
		color: $white;
		font-size: 30px;
		font-weight: 300;
		line-height: 30px;
		letter-spacing: 0.9px;
		margin-bottom: 20px;
		&::after {
			position: absolute;
			top: 58%;
			right: 10px;
			content: url(../img/plus.svg);
			transform: translate(0, -50%);
			display: none;
			@include sm {
				display: block; } }
		.is-open.ff-section__col & {
			&::after {
				position: absolute;
				top: 43%;
				right: 10px;
				transform: translate(0, -50%);
				content: url(../img/minus.svg); } }
		@include sm {
			width: 93%;
			padding: 20px 0;
			margin: 0 15px;
			border-bottom: 2px solid rgba(255,255,255, 0.5);
			font-size: 18px; } }
	&__price-program {
		color: $white;
		font-size: 18px;
		font-weight: 300;
		line-height: 30px;
		letter-spacing: 0.9px;
		@include sm {
			font-size: 14px; } }
	&__up-block {
		margin-top: 20px;
		.is-open.ff-section__col & {
			@include sm {
				display: block; } }
		@include sm {
			display: none; } }
	&__cont {
		margin-top: 20px;
		border-radius: 10px;
		background-color: $white;
		padding: 20px 0;
		title-xs {}
		@include sm {
			border-radius: 0; }
		.title-xs {
			color: $dark;
			font-size: 18px;
			font-weight: 700;
			line-height: 30px;
			letter-spacing: 0.9px; } }
	&__price {
		padding: 0 40px;
		text-align: left;
		color: $dark;
		font-size: 14px;
		font-weight: 300;
		letter-spacing: 0.7px;
		line-height: 30px; }
	&__list {
		position: relative;
		padding: 15px 0 15px 40px;
		border-bottom: 2px solid rgba(0,0,0, 0.05);
		&::before {
			position: absolute;
			content: url(../img/icon-check.png);
			width: 25px;
			height: 20px;
			top: 30px;
			left: 0px;
			transform: translate(0, -50%); } }
	&__sale {
		display: block;
		margin: 50px 0 37px;
		color: $crimson;
		font-family: $Panton;
		font-size: 30px;
		font-weight: 400;
		line-height: 30px;
		letter-spacing: 0.3px; }
	&__price-btn {
		background: none;
		padding: 23px 60px;
		border-radius: 30px;
		border: 2px solid $red;
		background-color: $red;
		color: $white;
		font-size: 11px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1.1px;
		margin-bottom: 37px;
		transition: background-color 0.3s, color 0.3s;
		&:hover {
			background-color: $white;
			color: $red; } }
	&__price-text {
		padding: 0 40px;
		color: $dark;
		font-family: $SFUIDisplay;
		font-size: 12px;
		font-weight: 300;
		line-height: 24px;
		letter-spacing: 0.6px; }
	&__sale-program {
		margin: 70px 15px 50px;
		.title-md {
			margin-bottom: 65px; } }
	&__more-info {
		position: relative;
		background: none;
		padding: 0 20px 5px 0;
		margin-left: 15px;
		color: $grey;
		font-size: 11px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1.1px;
		border-bottom: 2px solid $grey;
		display: none;
		&::after {
			position: absolute;
			top: 35%;
			right: -2px;
			content: url(../img/icon_arrow.svg);
			width: 7px;
			height: 7px;
			transform: rotate(90deg) translate(-50%, 50%); }
		&:hover {
			border-bottom: none; }
		@include sm {
			display: block; } }
	&__sale-wrap {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		@include md {
			display: flex;
			flex-direction: column;
			justify-content: center; }
		@include sm {
			display: none; }
		// +rmin(768)
 }		// 	display: block !important

	&__center-image {
		width: 310px;
		margin: 0 auto;
		@include md {
			margin: 40px auto; }
		img {
			width: 100%; } }
	&__left-column {
		max-width: 375px;
		.title-sm {
			color: $red;
			font-family: $Panton;
			font-size: 30px;
			font-weight: 400;
			line-height: 30px;
			letter-spacing: 0.3px; }
		@include md {
			margin: 0 auto 0 0; } }
	&__right-column {
		text-align: right;
		max-width: 365px;
		.title-sm {
			color: $red;
			font-family: $Panton;
			font-size: 30px;
			font-weight: 400;
			line-height: 30px;
			letter-spacing: 0.3px; }
		@include md {
			margin: 0 0 0 auto; } }
	&__column-text {
		color: $grey;
		font-family: $SFUIDisplay;
		font-size: 18px;
		font-weight: 300;
		line-height: 30px;
		letter-spacing: 0.9px; } }
