.reviews {
	&__inner {
		@include md {
			max-width: 540px;
			margin: 0 auto; }
		@include sm {
			padding: 0 135px; }
		@include xs {
			padding: 0 15px; } }
	&__title {
		max-width: 690px;
		.title-lg {
			margin-bottom: 57px; } }
	&__wrapper {
		padding-top: 77px;
		position: relative; }
	&__yellow-ellipse-right-top {
		position: absolute;
		width: 297px;
		height: 295px;
		top: 38px;
		right: 17%;
		transform: translate(50%, 0);
		z-index: -1;
		img {
			width: 100%; } }
	&__yellow-ellipse-left-top {
		position: absolute;
		width: 339px;
		height: 339px;
		top: 263px;
		left: 0;
		transform: translate(-50%, 0);
		z-index: -1;
		img {
			width: 100%; } }
	&__yellow-ellipse-right-bottom {
		position: absolute;
		width: 297px;
		height: 295px;
		bottom: 602px;
		left: 0;
		transform: translate(-50%, 0);
		z-index: -1;
		img {
			width: 100%; } }
	&__yellow-ellipse-left-bottom {
		position: absolute;
		width: 297px;
		height: 295px;
		top: 817px;
		right: 0;
		transform: translate(50%, 0);
		z-index: -1;
		img {
			width: 100%; } }
	&__row {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__wrap {
		text-align: center;
		margin-right: 80px;
		.radius-slider {
			&.radius-slider_right {
				.radius-slider__prev {
					top: 91%;
					left: 71%;
					@include sm {
						top: 95%;
						left: 62%; } }
				.radius-slider__next {
					top: 75%;
					left: 91%; } }
			.radius-slider__slide {
				position: relative;
				&::after {
					position: absolute;
					content: url(../img/client.png);
					top: 0;
					right: 5px;
					width: 154px;
					height: 154px;
					border-radius: 50%;
					background-size: cover;
					border: 2px solid $white; } } }
		.name {
			color: $dark;
			font-family: $SFUIDisplay;
			font-size: 14px;
			font-weight: 300;
			line-height: 30px;
			letter-spacing: 0.7px; } }
	&__block {
		color: $dark;
		font-family: $SFUIDisplay;
		font-size: 14px;
		font-weight: 300;
		line-height: 30px;
		letter-spacing: 0.9px; }
	&__title-text {
		font-size: 18px;
		margin-bottom: 50px; }
	&__line {
		display: block;
		width: 100%;
		height: 1px;
		margin: 50px 0;
		background-color: $dark;
		opacity: 0.05; }
	&__pages-block {
		display: flex;
		align-items: center;
		justify-content: center; }
	&__prev-btn {
		background: none;
		font-family: $SFUIDisplay;
		color: #bbbbbb;
		font-size: 10px;
		font-weight: 700;
		line-height: 24px;
		text-transform: uppercase;
		letter-spacing: 1px; }
	&__next-btn {
		background: none;
		font-family: $SFUIDisplay;
		color: $dark;
		font-size: 10px;
		font-weight: 700;
		line-height: 24px;
		text-transform: uppercase;
		letter-spacing: 1px; } }
